.ol-control.ol-bar .ol-rotate
{	opacity:1;
	visibility: visible;
}
.ol-control.ol-bar .ol-rotate button
{	display: block
}

/* Active buttons */
.ol-control.ol-bar .ol-toggle.ol-active > button
{	background: rgba(60, 136, 0, 0.7)
}
.ol-control.ol-bar .ol-toggle.ol-active button:hover
{	background: rgba(60, 136, 0, 0.7)
}
.ol-control.ol-toggle button:disabled
{	background: rgba(0,60,136,.3);
}

/* Subbar toolbar */
.ol-control.ol-bar .ol-control.ol-option-bar 
{	display: none;
	position:absolute;
	top:100%;
	left:0;
	margin: 5px 0;
	border-radius: 0;
	background: rgba(255,255,255, 0.8);
	/* border: 1px solid rgba(0, 60, 136, 0.5); */
	-webkit-box-shadow: 0 0 0 1px rgba(0, 60, 136, 0.5), 1px 1px 2px rgba(0, 0, 0, 0.5);
	        box-shadow: 0 0 0 1px rgba(0, 60, 136, 0.5), 1px 1px 2px rgba(0, 0, 0, 0.5);
}
.ol-control.ol-bar .ol-option-bar:before
{	content: "";
	border: 0.5em solid transparent;
	border-color: transparent transparent rgba(0, 60, 136, 0.5);
	position: absolute;
	bottom: 100%;
	left: 0.3em;
}
.ol-control.ol-bar .ol-option-bar .ol-control
{	display: table-cell;
}
.ol-control.ol-bar .ol-control .ol-bar
{	display: none;
}
.ol-control.ol-bar .ol-control.ol-active > .ol-option-bar 
{	display: block;
}
.ol-control.ol-bar .ol-control.ol-collapsed ul
{	display: none;
}
.ol-control.ol-bar .ol-control.ol-text-button > button:hover,
.ol-control.ol-bar .ol-control.ol-text-button > button
{	background: none;
	color: rgba(0, 60, 136, 0.5);
	width: auto;
	min-width: 1.375em;
	margin: 0;
